(function constructDataLayers() {
  function DataLayerProtoOne() {
    (function createEventModule(target) {
      const listeners = {};
      const obj = target;

      obj.events = [];

      obj.on = function on(event, callback) {
        listeners[event] = listeners[event] || [];
        listeners[event].push(callback);
      };

      obj.off = function off(event, callback) {
        listeners[event] = listeners[event].filter(listener => (
          listener.toString() !== callback.toString()
        ));
      };

      obj.trigger = function trigger(event, data) {
        const dataObj = {};
        dataObj[event] = data;

        if (listeners[event]) {
          listeners[event].forEach(listener => listener(dataObj));
          this.events.push(dataObj);
        }
      };
    }(this));

    (function createDataModule(target) {
      function deepmerge(...args) {
        const result = {};

        function merge(_obj) {
          Object.keys(_obj).forEach((prop) => {
            if (Object.prototype.hasOwnProperty.call(_obj, prop)) {
              if (Object.prototype.toString.call(_obj[prop]) === '[object Object]' && Object.prototype.hasOwnProperty.call(_obj[prop])) {
                result[prop] = deepmerge(result[prop], _obj[prop]);
              } else {
                result[prop] = _obj[prop];
              }
            }
          });
        }

        args.forEach(arg => merge(arg));

        return result;
      }

      const obj = target;

      obj.data = {
        page: {},
        user: {},
        ecommerce: {},
      };

      obj.set = function set(data) {
        const prevData = this.data;
        this.data = deepmerge(prevData, data);
      };
    }(this));
  }

  function DataLayerProtoTwo() {
    this.data = {
      page: {},
      user: {},
      ecommerce: {},
    };
    this.events = [];

    function handleEvent(array, arrayLength, newElementInArray) {
      console.log('array: ', array);
      console.log('arrayLength', arrayLength);
      console.log('array[arrayLength]: ', array[arrayLength]);
      console.log('newElementInArray: ', newElementInArray);
    }

    Object.defineProperty(this.events, 'push', {
      value: function pushWithBenefits(...args) {
        args.forEach((arg) => {
          handleEvent(this, this.length, this[this.length] = arg);
        });
        return this.length;
      },
    });
  }

  window.DataLayerProtoTwo = new DataLayerProtoTwo();
  window.DataLayerProtoOne = new DataLayerProtoOne();
}());
